@import "../scssVariable/scssVariable.scss";

@mixin cards{
    margin: 40px 20px 0px 20px;
    height: 500px;
    width: 350px;
    border-radius: 8px;
    background-size:cover;
    background-position: center;
    color: #dfdfdf;

    @media(max-width:500px){
        margin: 40px 0% 0px 0%;
        width: 90vw;
    }
}
.main{
    margin: 80px 0px 160px 0px;
    @media(max-width:500px){
        margin: 80px 0px 80px 0px;
    }
}
.cardField{
    display: flex;
    justify-content: center;
    @media(max-width:500px){
        display: grid;
    }
}
.cardText{
    display: grid;
    background-color: rgba(9, 104, 199, 0.56);
    font-size: 20px;
    font-weight: 500;
    border-radius: 8px 8px 0px 0px;
    height: 50px;
    align-content: center;
}
.card1{
    @include cards;
    background-image: url('../../img/personal.jpg');
    
}
.card2{
    @include cards;
    background-image: url('../../img/progtren.jpg');
}
.card3{
    @include cards;
    background-image: url('../../img/group2.jpg');
}
