@import "../scssVariable/scssVariable.scss";

.main{
    display: grid;
    justify-items: center;
    // background-image: url('../../img/ghg5556.png');
    // background-size:cover;
    // background-position: center;
    margin-top: 80px;
}
.blockContact{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    margin: 60px 0px;
    @media(max-width:790px){
        grid-template-columns: 1fr;
        margin: 30px 0px;
    }
}
.AdressAndTel{
    display: grid;
    align-items: center;
}
.contact{
    display: grid;
    width: 300px;
    background-color:rgb(25, 25, 25);;
    border-radius: 8px;
    font-size: 22px;
    color: rgb(255, 255, 255);
    padding: 40px;
    @media(max-width:790px){
        width: 70vw;
    }
}
.imgGira{
    justify-self: center;
    height: 250px;
    width: 250px;
    background-image: url('../../img/gyra.png');
    background-size:cover;
    background-position: center;
    @media(max-width:969px){
        height: 200px;
        width: 200px;
    }
    @media(max-width:790px){
        display: none;
    }
}
.imgGant{
    justify-self: center;
    height: 250px;
    width: 250px;
    background-image: url('../../img/gyra2.png');
    background-size:cover;
    background-position: center;
    @media(max-width:969px){
        height: 200px;
        width: 200px;
    }
}

.blockTelegWhat{
    display: grid;
    justify-self: center;
    grid-template-columns: 1fr 1fr;
}
.telegram{
    width: 40px;
    height: 40px;
    margin: 20px 10px 20px 10px;
}
.whatsapp{
    width: 40px;
    height: 40px;
    margin: 20px 10px 20px 10px;
}
.tel{
    text-decoration: none;
    color: white;
    &:hover{
        color: rgb(90, 132, 215);
    }
}