@import "../scssVariable/scssVariable.scss";

.main{
    margin-top: 80px;
}
.blockPrice{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2%;

    @media(max-width: 500px){
        grid-template-columns: 1fr;
    }
}
.person{
    display: grid;
    justify-self: end;
    background-image: url('../../img/1614411890_15-p-temnii-fon-metall-20.jpg');
    background-size:cover;
    background-position: center;
    width: 70%;
    margin-top: 40px;
    border-radius: 8px;
    @media(max-width: 500px){
        grid-template-columns: 1fr;
        justify-self: center;
        width: 90vw;
    }
}
.group{
    display: grid;
    justify-self: start;
    background-image: url('../../img/1614411890_15-p-temnii-fon-metall-20.jpg');
    background-size:cover;
    background-position: center;
    width: 70%;
    margin-top: 40px;
    border-radius: 8px;
    @media(max-width: 500px){
        grid-template-columns: 1fr;
        justify-self: center;
        width: 90vw;
    }
}
.textPrice{
    display: grid;
    height: 40px;
    border-radius: 8px 8px 0px 0px;
    background-color: rgba(13, 115, 240, 0.398);
    color: #ececec;
    align-content: center;
    font-size: 24px;
    @media(max-width: 500px){
        font-size: 18px;
    }
}
.price{
    border-radius: 0px 0px 8px 8px;
}
.priceElem{
    text-align: left;
    color: #fbfbfb;
    margin: 20px 0px 20px 40px;
    height: 20px;
    
}
.priceNone{
    
}