$radius: 8px;
$procent: 30%;

@mixin slideBehind {
    color: rgb(213, 228, 248);
    font-size: 28px;
    background-size:cover;
    background-position: center;
    display: grid;
    align-items:end;
    height: 400px;
    width:29%;
    margin: 0px 1% 0px 1%;
    border-radius: $radius;
    transition: 1s;
    @media(max-width:500px){
        width: 0%;
        margin: 0px 0% 0px 0%;
      }
}

@mixin  left{
    background-image: url('../../img/b1.png');
    @include slideBehind;
}
@mixin slideCenter {
    background-image: url('../../img/b2.png');
    background-size:cover;
    background-position: center;
    height: 500px;
    width:38%;
    border-radius: $radius;
    color: beige;
    font-size: 28px;
    display: grid;
    align-items:end;
    transition: 1s;
    @media(max-width:500px){
        width: 100%;
      }
}
@mixin right{
    background-image: url('../../img/b4.png');
    @include slideBehind;
}


.main{
    display: grid;
    background-color: rgb(45, 45, 45);
}
.blockSlide{
    display: flex;
    align-items: center;
    height: 502px;
    overflow: hidden;
}


.slide11{
    @include left;
    z-index: 2;
}
    .slide12{
        @include left;
        transform: translateX(79%);
        height: 500px;
        width:38%;
        z-index: 2;
        color: beige;

        @media(max-width:500px){
            transform: translateX(0%);
            width: 100%;
        }
    }
    .slide13{
        @include left;
        transform: translateX(calc(238%));
        height: 400px;
        width:29%;
        z-index: 3;

        @media(max-width:500px){
            transform: translateX(calc(0%));
            width: 0%;
            z-index: 2;
        }
    }
.slide21{
    @include slideCenter;
    z-index: 2;
}
    .slide22{
        @include slideCenter;
        transform: translateX(103.5%);
        height: 400px;
        width:29%;
        z-index: 1;
        color: rgb(213, 228, 248);

        @media(max-width:500px){
            transform: translateX(0%);
            width: 0%;
        }
    }
    .slide23{
        @include slideCenter;
        transform: translateX(-104%);
        height: 400px;
        width:29%;
        z-index: 1;
        color: rgb(213, 228, 248);

        @media(max-width:500px){
            transform: translateX(0%);
            width: 0%;
        }
    }
.slide31{
    @include right;
    z-index: 0;
}   
    .slide32{
        @include right;
        transform: translateX(-238%);
        height: 400px;
        width:29%;
        z-index: 0;

        @media(max-width:500px){
            transform: translateX(0%);
            width: 0%;
        }
    }   
    .slide33{
        @include right;
        transform: translateX(-79%);
        height: 500px;
        width:38%;
        z-index: 2;
        color: beige;

        @media(max-width:500px){
            transform: translateX(0%);
            width: 100%;
            z-index: 4;
        }
    }   

.sldText{
    background-color: rgba(0, 0, 0, 0.689);
    border-radius: 0px 0px 8px 8px;
}
.btnLeft{
    display: grid;
    position: absolute;
    align-self: center;
    height: 60px;
    width: 30px;
    margin-left: 15px;
    cursor: pointer;
    z-index: 5;
    @media(max-width:500px){
        height: 40px;
        width: 20px;
    }
}
.btnRight{
    display: grid;
    align-self: center;
    justify-self: end;
    position: absolute;
    height: 60px;
    width: 30px;
    margin-right: 15px;
    cursor: pointer;
    z-index: 5;
    @media(max-width:500px){
        height: 40px;
        width: 20px;
    }
}