@import "../scssVariable/scssVariable.scss";
.main{
    background-color: #1c1c1c;
    color: aliceblue;
    height: 60px;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 4fr 1fr;
    align-items: center;
    z-index: 12;
    @media(max-width: 550px){
        grid-template-columns: 1fr;
     }
}
.category{
    @media(max-width: 550px){
       display: none;
    }
}
.elemCateg{
    text-decoration: none;
    color: rgb(237, 237, 237);
    font-size: 19px;
    font-weight: 600;
    padding: 0px 14px 0px 14px;
}
    .elemCateg:hover{
    color: rgb(90, 132, 215);
    }
.blockContact{
    display: grid;
    justify-self: end;
    grid-template-columns: 50px 50px;
    margin-right: 20px;
    @media(max-width: 550px){
        display: none;
     }
}
.img{
    height: 55px;
}
