$fieldColor: rgba(65, 69, 77, 0.745);
.header{
   color: rgb(255, 255, 255);
   font-size: 28px; 
   font-weight: 500;
   margin-top: 20px;
   @media(max-width:490px){
      font-size: 26px; 
   }
}

.telegram{
   display: grid;
   background-image: url('../../img/telegram1.png');
   background-size:cover;
   background-position: center;
   height: 30px;
   width: 30px;
   &:hover{
       background-image: url('../../img/telegram4.png');
   }
}
.whatsapp{
   display: grid;
   background-image: url('../../img/whatapp1.png');
   background-size:cover;
   background-position: center;
   height: 30px;
   width: 30px;
   &:hover{
       background-image: url('../../img/whatapp4.png');
   }
}
@media (prefers-reduced-motion: no-preference){
   :root{
      scroll-behavior: smooth;
   }
}
