@import "../scssVariable/scssVariable.scss";
.main{
    display: grid;
    justify-items: center;
    margin-top: 80px;
    padding-bottom: 40px;
    @media(max-width:932px){
      margin-top: 80px;
    }
}
.blocks{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 460px;
    margin-top: 40px;
    background-image: url('../../img/fon333.jpg');
    background-size:cover;
    background-position: center;
    border-radius: 8px;

    @media(max-width:932px){
      width: 99vw;
      margin-top: 40px;
      grid-template-columns:1fr;
      height:auto;
    }
}
.blockText{
    align-self: center;
    display: grid;
    margin-left: 20px;
    @media(max-width:932px){
      margin-left: 0px;
      margin-bottom: 20px;
    }
}
.textName{
    padding: 8px;
    font-size: 26px;
    font-weight: 700;
    background-color: rgba(13, 115, 240, 0.398);
    border-radius: 8px 8px 0px 0px;
    color: rgb(255, 255, 255);
    border-radius: 8px;
}
.blockImg{
    display: grid;
    justify-self: center;
    align-self: center;
    height: 430px;
    width: 430px;
    background-image: url('../../img/photo.png');
    background-size:cover;
    background-position: center;
    @media(max-width:932px){
      margin-bottom: 20px;
    }
    @media(max-width:480px){
      height: 350px;
      width: 350px;
    }
}
@keyframes blockImg {
    0% {
      transform: scale(0, .025);
    }
    50% {
      transform: scale(1, .025);
    }
  }
  h2{
    color:white;
  }
  h3{
    color:rgb(234, 234, 234);
    margin-bottom: 28px;
  }
  h4{
    color:rgb(195, 213, 237);
    margin: 15px;
    @media(max-width:932px){
      margin: 8px;
    }
  }