@import "../scssVariable/scssVariable.scss";

.main{
    background-color: rgb(28, 28, 28);
    color: aliceblue;
    // box-shadow: 0px -4px 10px  rgb(0, 0, 0);
}
.line{
    background-color: #3d76e0;
    height: 2px;

}
.block{
    display: flex;
    margin-top: 20px ;
    padding-bottom: 20px;
    justify-content: center;
    @media(max-width: 500px){
        display: grid;
    }
}
.img{
    height: 30px;
}
.elemCateg{
    text-decoration: none;
    color: rgb(237, 237, 237);
    font-size: 17px;
    font-weight: 600;
    padding: 0px 14px 0px 14px;
    &:hover{
        color: rgb(90, 132, 215);
    }
    @media(max-width: 500px){
        font-size: 18px;
        padding: 5px 14px 5px 14px;
    }
}
.blockTelgWhat{
    display: flex;
    justify-content: center;
}
.telegram{
    height: 25px;
    width: 25px;
    margin-left: 16px;
    @media(max-width: 500px){
        margin: 5px;
        height: 30px;
        width: 30px;
    }
 }
 .whatsapp{
    height: 25px;
    width: 25px;
    margin-left: 12px;
    @media(max-width: 500px){
        height: 30px;
        width: 30px;
        margin: 5px;
    }
 }