@import "../scssVariable/scssVariable.scss";
.main{
    display: grid;
    justify-items: center;
    margin-top: 80px;
    @media(max-width:500px){
        margin-top: 40px;
    }
}
.header{
    margin-bottom: 40px;
}
.blockSystem{
    display: grid;
    justify-items: center;
    background-image: url('../../img/1692379633_pushinka-top-p-sportivnii-zal-kartinki-krasivo-61.jpg');
    background-size: cover;
    background-position: center;
    border-radius: 8px;
    
}
.system{
    display:grid;
    justify-items: center;
    border-radius: 8px;
    background-color: rgba(32, 32, 32, 0.819);
    grid-template-columns: 1fr 1fr 1fr;
    height: auto;
    width: 100%;
    margin: 0px;
    padding: 0px;
    @media(max-width:1200px){
        grid-template-columns: 1fr 1fr;
    }
    @media(max-width:800px){
        grid-template-columns: 1fr;
        width: 90vw;
    }
    @media(max-width:500px){
        margin: 0px 0% 0px 0%;
        width: 90vw;
    }
    
}
.blockElem{
    display: grid;
    height: 350px;
    width: 400px;
    grid-template-rows: auto auto;
    background-color: rgba(14, 96, 167, 0.46);
    border-radius: 8px;
    margin: 20px 20px 20px 20px;
    cursor: pointer;
    &:hover{
        background-color: rgba(26, 115, 192, 0.356);
    };
    @media(max-width:1370px){
        width: 350px;
    }
    @media(max-width:800px){
        width: 400px;;
    }
    @media(max-width:490px){
        width: 330px;;
    }
}
.blockImg{

}
.elemText{
    display: grid;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    text-align:center;
    color: rgb(255, 255, 255);
    margin: 20px 20px 20px 20px;
}
h2{
    font-size: 26px;
    color: rgb(104, 183, 248);
}
.text{
    display: none;
}

.textImg{
    font-size: 26px;
    color: azure;
    margin-bottom: 10px;
    font-weight: 500;
}

@mixin img{
    border-radius: 8px;
    height: 300px;
    background-size:cover;
    background-position: center;
}   
.img1{
    @include img;
    background-image: url('../../img/system/1s.png');
}
.img2{
    @include img;
    background-image: url('../../img/system/2s.png');
}
.img3{
    @include img;
    background-image: url('../../img/system/3a.png');
}
.img4{
    @include img;
    background-image: url('../../img/system/13a.png');
}
.img5{
    @include img;
    background-image: url('../../img/system/15s.png');
}